import React from 'react'
import LcmLogo from '../img/gitter.svg'
import FaReddit from 'react-icons/lib/fa/reddit'
import './HomeTopBar.scss'

export default function HomeTopBar() {
  return (
    <div className="HomepageTopBar">
      <a
        className="logo"
        href="https://gitter.im/rigoblock-drago/Lobby"
        title="Chat with us on Gitter"
        target="_blank"
      >
        <img src={LcmLogo} alt="RigoBlock Logo" />
      </a>
      <a
        className="maintained"
        href="https://gitter.im/rigoblock-drago/Lobby"
        target="_blank"
      >
        Chat with us on Gitter
      </a>
      <a
        className="blog"
        href="https://medium.com/rigoblock"
        target="_blank"
      >
        Blog
      </a>
      <a
        className="reddit"
        href="https://www.reddit.com/r/rigoblock/"
        target="_blank"
      >
        Reddit
      </a>
    </div>
  )
}



