import React from 'react'
import Link from 'gatsby-link'
//import GabLogo from '../img/gab2.jpg'
//import HannaLogo from '../img/Hanna.png'
//import DavidLogo from '../img/david400.jpg'
import './ExtensionSelection.scss'

export default function ExtensionSelectionJobs({ className }) {
  return (
    <ul className={`ExtensionSelection ${className}`}>
      <li>
        <a
          href="mailto:admin@rigoblock.com?Subject=RigoBlock%20contact" target="_top"
          target="_blank"
          className="Extension"
        >
          <h4>UX and UI Designer</h4>
          <p>You are experienced with React and ES8. You have an interest in decentralized architectures. You get a horizontal and incentives-driven structure. Remote position possible.</p>
        </a>
      </li>
      <li>
        <a
          href="mailto:admin@rigoblock.com?Subject=RigoBlock%20contact" target="_top"
          target="_blank"
          className="Extension"
        >
          <h4>Solidity Developer</h4>
          <p>You can deliver exceptional Solidity code. You will contribute to improving and expanding our protocol. You get a decentralized, horizontal and incentives-driven structure. Remote position possible.</p>
        </a>
      </li>
      <li>
        <a
          href="mailto:admin@rigoblock.com?Subject=RigoBlock%20contact" target="_top"
          target="_blank"
          className="Extension"
        >
          <h4>Community Manager</h4>
          <p>You will be in charge of managing our community and improving our presence on social media and our communication channels. You can produce outstanding content. Remote position possible.</p>
        </a>
      </li>
    </ul>
  )
}

