import React from 'react'
import Link from 'gatsby-link'
import TopBar from '../components/HomeTopBar'
import Footer from '../components/Footer'
import Branding from '../components/Branding'
import Bubble, {
  BubbleLayer,
  BubbleCluster,
  BubbleLayerBoundary,
} from '../components/Bubble'
import MouseIcon from '../components/MouseIcon'
import ExtensionSelection from '../components/ExtensionSelection'
import ExtensionSelectionJobs from '../components/ExtensionSelectionJobs'
import Manny from '!svg-react-loader?name=Logo!../img/Logo.svg'
import Page from '../components/Page'
import Helmet from 'react-helmet'

export default function IndexPage() {
  const tree = [
    { to: '/', title: 'Blockchain Token Pools', below: [] },
  ]
  return (
    <div className="HomeWrapper">
      <Helmet>
        <title>RigoBlock | Blockchain Token Pools</title>
        <meta name="description" content="RigoBlock allows users to create blockchain token pools. Use our decentralized applications on Ethereum" />
        <meta name="keywords" content="blockchain token pool, token pool, ethereum mining, asset management, mutual funds, online share trading, managed funds, alternative investments, day trading strategies, top hedge funds, investment management, asset manager, good stocks to invest in, hedge fund companies, what are hedge funds, hedge fund manager, proof of stake mining, proof of stake, decentralized exchange" />
      </Helmet>
      <div className="Homepage">
        <div className="IntroPane">
          <TopBar />
          <HomepageHero />
          <MouseIcon />
        </div>
        <AboutProductPane />
        <GettingStartedPane />
        <Careers />
      </div>
      <Footer />
    </div>
  )
}

function HomepageHero() {
  return (
    <BubbleLayerBoundary className="HomepageHero">
      <div className="inner">
        <Branding large slogan />
        <ul className="links">
          <li>
            <a href="#GetStarted" className="button dashing-icon expanded">
              <span className="text">Team</span>
            </a>
          </li>
          <li>
            <a
              href="https://github.com/RigoBlock/whitepaper/raw/master/RigoBlockPaper.pdf"
              className="button dashing-icon expanded"
            >
              <i className="icon icon-document" />
              <span className="text">Whitepaper</span>
            </a>
          </li>
          <li>
            <a
              href="https://pool.rigoblock.com"
              target="_blank"
              className="button blue expanded"
            >
              <span className="text">Our Platform</span>
            </a>
          </li>
        </ul>
      </div>

      <BubbleLayer travel={5}>
        <Bubble
          size={130}
          thickness={20}
          duration={35}
          blur={2}
          top="110%"
          left="0"
          bottom="0"
          opacity={0.45}
        />
        <Bubble
          size={38}
          thickness={11}
          duration={9}
          blur={2}
          top="100%"
          right="6%"
          opacity={0.45}
        />
      </BubbleLayer>

      <BubbleLayer travel={15}>
        <Bubble
          size={85}
          thickness={13}
          duration={15}
          blur={6}
          top="105%"
          left="4%"
          opacity={0.25}
        />
        <Bubble
          size={75}
          thickness={13}
          duration={10}
          blur={6}
          top="30%"
          right="13%"
          opacity={0.25}
        />
      </BubbleLayer>

      <BubbleLayer travel={30}>
        <Bubble
          size={35}
          thickness={8}
          duration={9}
          blur={4}
          top="15%"
          left="15%"
          opacity={0.07}
        />
        <BubbleCluster duration={15} left="40%" top="110%">
          <Bubble size={30} thickness={10} blur={5} opacity={0.07} />
          <Bubble size={40} thickness={13} blur={5} opacity={0.07} />
        </BubbleCluster>
      </BubbleLayer>
    </BubbleLayerBoundary>
  )
}

function AboutProductPane() {
  return (
    <div className="AboutProductPane">
      <BubbleLayerBoundary className="inner">
        <div className="note">
          <h2 id="AboutUs">We build software for organizing token holdings</h2>
          <p>
            RigoBlock allows anyone to create Blockchain Token Pools easily and implement digital token strategies
          </p>
          <a href="https://rigo.network" target="_blank" className="button dashing-icon">
            <i className="icon icon-right" />
            <span className="text">Explore our IPFS dapps</span>
          </a>
        </div>
        <div className="shot-casing">
          <div className="shot" />
        </div>
      </BubbleLayerBoundary>
    </div>
  )
}

function GettingStartedPane() {
  return (
    <div className="GetStartedPane">
      <h2 id="GetStarted">Our Team</h2>
      <div className="ChooseExtensionStep Step">
        <ExtensionSelection />
      </div>
    </div>
  )
}

function Careers() {
  return (
    <div className="GetStartedPane">
      <h2 id="Careers">Careers</h2>
      <div className="ChooseExtensionStep Step">
        <ExtensionSelectionJobs />
      </div>
    </div>
  )
}

